/* *{
    margin: 0;
    padding: 0;
} */
@font-face {
  font-family: ser-con-btn;
  src: url("../../utils/fonts/Metropolis-Regular.otf");
}
.ul {
    padding: 0;
    margin: 0;
  }
  .li {
    list-style: none;
  }
  body {
    background-color: #eee;
    font-family: "Open Sans", sans-serif;
  }
  
  /* width */
  ::-webkit-scrollbar {
    width: 3px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
    display: none;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #333;
    border-radius: 10px;
    display: none;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #333;
    display: block;
  }
  
  .side-menu {
    position: fixed;
    background: rgb(139, 139, 139);
    width: 300px;
    height: 100%;
    box-sizing: border-box;
    padding: 30px 20px;
    transition: width 0.2s ease-in;
  }
  
  .side-menu.inactive {
    width: 80px;
  }
  
  .side-menu .top-section {
    position: relative;
  }
  
  .side-menu .top-section .logo {
    width: 70px;
    height: 70px;
    overflow: hidden;
    display: inline-block;
  }
  .side-menu .top-section .logo img {
    max-width: 100%;
    max-height: 100%;
  }
  
  .side-menu .top-section .toggle-menu-btn {
    color: #666;
    font-size: 20px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    cursor: pointer;
  }
  
  .side-menu.inactive .top-section .toggle-menu-btn {
    right: -50px;
  }
  
  .side-menu .main-menu .menu-item {
    color: #333;
    text-decoration: none;
    font-size: 15px;
    display: block;
    font-weight: 600;
    cursor: pointer;
  }
  
  .side-menu .main-menu .menu-item.active {
    background: #333;
    border-radius: 5px;
    color: #fff;
  }
  
  .side-menu .main-menu {
    margin: 5px 0;
    position: relative;
    max-height: 450px;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  /* not required any more */
  .side-menu.inactive .main-menu {
  }
  
  .side-menu .main-menu .menu-item .menu-icon {
    display: inline-block;
    width: 40px;
    height: 40px;
    font-size: 20px;
    line-height: 40px;
    text-align: center;
  }
  
  .side-menu .main-menu .menu-item span {
    position: absolute;
    display: inline-block;
    line-height: 40px;
    opacity: 1;
    font-family: ser-con-btn;
    transition: opacity 0.2s ease-in;
  }
  
  .side-menu.inactive .main-menu .menu-item span {
    opacity: 0;
    width: 0;
    height: 0;
    overflow: hidden;
  }

  .divider {
    width: 100%;
    height: 1px;
    border-radius: 1px;
    background: #333;
  }
  
  .container {
    margin-left: 300px;
    transition: margin-left 0.2s ease-in;
    padding: 50px;
    box-sizing: border-box;
  }
  
  .container.inactive {
    margin-left: 80px;
  }

  